import DdzPathHelper from "./DdzPathHelper";

export default class DdzSkinDefine{
    public static readonly jsonPath = DdzPathHelper.configPath + "DdzSkinConfig";

    /**
     * 皮肤ID
     */
    public skinIndex :number = 0;
    /** 记牌器文本颜色 0为数量0颜色 1为数量不为0颜色 */
    public markerColorArr: string[] = [];
    /** 使用角色替代玩家头像 */
    public useRole: boolean = false;

    constructor(public jsonMap: {} = {}){
        this.initData();
    }

    private initData(){
        this.skinIndex = this.jsonMap["skinIndex"] || 0;
        this.markerColorArr = this.jsonMap["markerLblColor"] || [];
        this.useRole = this.jsonMap["useRole"];
    }
}